import React, { Component } from 'react'
import { HashRouter, Route, Switch } from 'react-router-dom'

const View = React.lazy(() => import('./View'))
const Insert = React.lazy(() => import('./Insert'))
const Update = React.lazy(() => import('./Update'))
const Detail = React.lazy(() => import('./Detail'))


class Product extends Component {
    render() {
        return (
            <HashRouter>
                <React.Suspense>
                    <Switch>
                        <Route exact path="/product/insert" render={props => <Insert {...props} />} />
                        <Route exact path="/product/update/:code" render={props => <Update {...props} />} />
                        <Route exact path="/product/detail/:code" render={props => <Detail {...props} />} />
                        <Route path="/product" render={props => <View {...props} />} />
                    </Switch>
                </React.Suspense>
            </HashRouter>
        )
    }
}

export default Product